export enum AssociatedWixIdentityConnectionStatusEnumConnectionStatus {
  /** There is no information about the connection status. */
  UNKNOWN = 'UNKNOWN',
  /** The Wix user is connected to the staff member. */
  CONNECTED = 'CONNECTED',
  /** The Wix user must accept the invitation to join **.wix.com.** or the site. */
  PENDING = 'PENDING',
  /** The invitation to join **.wix.com.** or the site has expired. */
  EXPIRED = 'EXPIRED',
  /** The Wix user was disconnected from the staff member. */
  DISCONNECTED = 'DISCONNECTED',
}

/** @enumType */
export type AssociatedWixIdentityConnectionStatusEnumConnectionStatusWithLiterals =

    | AssociatedWixIdentityConnectionStatusEnumConnectionStatus
    | 'UNKNOWN'
    | 'CONNECTED'
    | 'PENDING'
    | 'EXPIRED'
    | 'DISCONNECTED';

